/**
 * @file 发布商品-供货信息Table组件
 */
import React, { Component } from 'react';
import { Form,Button,Layout,Modal,Checkbox,Table,Col,Row,
         Popover,Radio,InputNumber,message,Input,Icon } from 'jdcloudui';
import RegionPriceEdit from './RegionPriceEdit';
import RegionPriceDetail from './RegionPriceDetail';

const { Content } = Layout;
const CheckboxGroup = Checkbox.Group;
const RadioGroup = Radio.Group;
const FormItem = Form.Item

let uuid = 0;
@Form.create()

export default class SupplyInfoTable extends Component {
  constructor(props){
    super(props);
    this.regionalArr=[],
    this.columns = [
      { title:<span>常规备货</span>,
        dataIndex: 'inventory',
        key: 'inventory',
        render:(text,record,index) =>(
          <div>
            {record.disabled ?
              <span style = {{color: '#bbb', float: 'left',marginRight: '5px', marginTop: '4px'}}>常规备货</span>
                : <span style = {{float: 'left',marginRight: '5px', marginTop: '4px'}}>常规备货</span>}
              <InputNumber disabled = {record.disabled} 
                          onChange = {(e) => {this.changeECount(index, e)}}
                          value = {this.props.itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index] ? 
                            this.props.itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].inventory : ''}
                           style = {{width: '100px', float: 'left'}}></InputNumber>
          </div>
      )
    },
    { title: <span>供货价</span>,
      dataIndex: 'sprice',
      key: 'sprice',
      render:(text,record, index) =>(
        <div>
           {record.disabled ?
             <span style = {{color: '#bbb', float: 'left',marginRight: '5px', marginTop: '4px'}}>供货价</span>
             : <span style = {{float: 'left',marginRight: '5px', marginTop: '4px'}}>供货价</span>}
              <InputNumber min={0.01}
                          disabled = {record.disabled}
                          onChange = {(e)=>this.changeESprice(e,index)}
                          onBlur = {(e)=>this.changeBESprice(e,index)}
                          value = {this.props.itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index] && 
                            this.props.itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].areaPriceList ?
                              this.props.itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].areaPriceList[0].supplyPrice:''}
                          style = {{width: '100px', float: 'left'}}></InputNumber>

          <span className="outSprice">
            {record.disabled ?
              <span className="tableSprice" style = {{color: '#bbb'}}>设置地域价</span>
              : <span style = {{float: 'left',marginRight: '5px', marginTop: '4px'}}>
              <RegionPriceEdit
                              index={index}
                              itemTmplPublishVo={this.props.itemTmplPublishVo}
                              updateItemTmplAction = {this.props.updateItemTmplAction}
                              getRegionalPrice={this.props.getRegionalPrice}
                              regionalArr={this.regionalArr}
                                >
                              </RegionPriceEdit>
                </span>}
         </span>
         <span className="outSprice">
          {record.disabled ?
            <span className="tableView" style = {{color: '#bbb'}}>查看</span>
            : <span style = {{float: 'left',marginRight: '5px', marginTop: '4px'}}>
            <RegionPriceDetail
              index={index}
              itemTmplPublishVo={this.props.itemTmplPublishVo}
              updateItemTmplAction = {this.props.updateItemTmplAction}
              getRegionalPrice={this.props.getRegionalPrice}
              regionalArr={this.regionalArr}
            >
            </RegionPriceDetail>
            </span>}
          </span>
        </div>
        )
    },
    { title: '供货量',
      dataIndex: 'count',
      key: 'count',
     render:(record, index) => 0
    },
    { title: '供货情况',
      dataIndex: 'supplyStatus',
      key: 'supplyStatus',
      render:(text,record, index) => {
        return(
          <RadioGroup onChange={(e)=>this.radioChange(index,e)} 
                      defaultValue = {text || 1}>
              <Radio value={0}>暂无供应</Radio>
              <Radio value={1}>正常供应</Radio>
          </RadioGroup>
          );
      }
    },
  ];
  this.columnsSku = [
    { title: 'SKU信息',
      dataIndex: 'attributes',
      key: 'attributes',
        width: 200,
        render: (data) => {
          var context = '';
          (data || []).forEach((item) => {
            context += item.vName + ' ';
          });
          return (<span>{context}</span>);
        }
      },
    { title:<span>常规备货</span>,
      dataIndex: 'inventory',
      key: 'inventory',
      render:(text,record,index) =>(
        <div>
          {record.disabled ?
            <span style = {{color: '#bbb', float: 'left',marginRight: '5px', marginTop: '4px'}}>常规备货</span>
              : <span style = {{float: 'left',marginRight: '5px', marginTop: '4px'}}>常规备货</span>}
            <InputNumber disabled = {record.disabled} 
                         onChange = {(e) => {this.changeECount(index, e)}}
                         value = {this.props.itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index] ? 
                          this.props.itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].inventory : ''}
                         style = {{width: '100px', float: 'left'}}></InputNumber>
        </div>
    )
  },
  { title: <span>供货价</span>,
    dataIndex: 'sprice',
    key: 'sprice',
    render:(text,record,index) =>(
      <div>
         {record.disabled ?
           <span style = {{color: '#bbb', float: 'left',marginRight: '5px', marginTop: '4px'}}>供货价</span>
           : <span style = {{float: 'left',marginRight: '5px', marginTop: '4px'}}>供货价</span>}
            <InputNumber min={0.01}
                        disabled = {record.disabled}
                        onChange = {(e)=>this.changeESprice(e,index)}
                        onBlur = {(e)=>this.changeBESprice(e,index)}
                        value = {this.props.itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index] && 
                          this.props.itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].areaPriceList ?
                            this.props.itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].areaPriceList[0].supplyPrice:''}
                        style = {{width: '100px', float: 'left'}}></InputNumber>

        <span className="outSprice">
          {record.disabled ?
            <span className="tableSprice" style = {{color: '#bbb'}}>设置地域价</span>
            : <span style = {{float: 'left',marginRight: '5px', marginTop: '4px'}}>
            <RegionPriceEdit
                            index={index}
                            itemTmplPublishVo={this.props.itemTmplPublishVo}
                            updateItemTmplAction = {this.props.updateItemTmplAction}
                            getRegionalPrice={this.props.getRegionalPrice}
                            regionalArr={this.regionalArr}
                              >
                            </RegionPriceEdit>
              </span>}
       </span>
       <span className="outSprice">
        {record.disabled ?
          <span className="tableView" style = {{color: '#bbb'}}>查看</span>
          : <span style = {{float: 'left',marginRight: '5px', marginTop: '4px'}}>
          <RegionPriceDetail
                index={index}
                itemTmplPublishVo={this.props.itemTmplPublishVo}
                updateItemTmplAction = {this.props.updateItemTmplAction}
                getRegionalPrice={this.props.getRegionalPrice}
                regionalArr={this.regionalArr}
              >
          </RegionPriceDetail>
          </span>}
        </span>
      </div>
      )
  },
  { title: '供货量',
    dataIndex: 'count',
    key: 'count',
   render:(record, index) => 0
  },
  { title: '供货情况',
    dataIndex: 'supplyStatus',
    key: 'supplyStatus',
    render:(text,record,index) => {
      console.log(text)
      return(
        <RadioGroup onChange={(e)=>this.radioChange(index,e)} 
        defaultValue = {text || 1}>
            <Radio value={0}>暂无供应</Radio>
            <Radio value={1}>正常供应</Radio>
        </RadioGroup>
        );
    }
  },
];
    this.initdata=[{
      key: 0,
      count: 0,
      totalPrice: null,//供货价
      inventory: null,//销量
      attributes: null,
    }],
    this.state={
      addressDATA:[],
      checkedList:[],
      afterChecked: [],
      optionsList:[],
      regionalList:[],
      initdata:this.initdata,
      inventory:{},
      supplyPrice:{},
      visible:false,
      visiblePop:{},
      visibleView:{},
      column:this.columns,
      modalKey:null,
    };
    this.lastKey = 0;
  }
  
  componentDidMount(){
    console.log(22)
    let itemTmplPublishVo = JSON.parse(JSON.stringify(this.props.itemTmplPublishVo));
    if(itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList && itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList.length>0
                                                        && itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[0].attributes){
      console.log(2222)
      this.setState({
        column:this.columnsSku,
        initdata : itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList
      })
    } else {
      if(itemTmplPublishVo.itemTmplSkuVoList && itemTmplPublishVo.itemTmplSkuVoList.length>0 &&
                                                  itemTmplPublishVo.itemTmplSkuVoList[0].attributes){
        let arrList = [];
        console.log(222)
        itemTmplPublishVo.itemTmplSkuVoList.map(item=>{
          if(item.skuStatus){
            arrList.push({
              attributes:item.attributes,
              key:item.key,
              supplyStatus:item.supplyStatus,
              areaPriceList:[{
                  "areaId":"0",
                  "areaName":"全国",
                  "areaNumber":1,//地域价序号
                  "supplyPrice":"",
              }],
            })
          }
        })
        itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList = arrList
        itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList.map(item=>{
          item.supplyStatus = 1
        });
        this.setState({
          column:this.columnsSku,
          initdata : itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList
        })
      this.props.updateItemTmplAction(itemTmplPublishVo)
      console.log(this.props.itemTmplPublishVo)
      }
    }
  }
  componentWillReceiveProps(next){
    let nextProps= JSON.parse(JSON.stringify(next));
    console.log(111)
    console.log(nextProps)
     this.initColumns(nextProps.itemTmplPublishVo)
    if(nextProps.regionalData.loaded){
      if(nextProps.regionalData.data.code==0){
        let regionalDATA = []
        for (var i in nextProps.regionalData.data.data){
          if(nextProps.regionalData.data.data[i]){
            regionalDATA = nextProps.regionalData.data.data[i]
          }
        }
        this.regionalArr = regionalDATA
      }
    }
  }

  initColumns(nextProps){
    if(nextProps.itemTmplSkuVoList && nextProps.itemTmplSkuVoList.length>0 && 
                                        nextProps.itemTmplSkuVoList[0].attributes){
      let arrList = []
      nextProps.itemTmplSkuVoList.map(item=>{
          if(item.skuStatus){
            arrList.push({
              attributes:item.attributes,
              key:item.key,
              supplyStatus:item.supplyStatus,
              areaPriceList:[{
                  "areaId":"0",
                  "areaName":"全国",
                  "areaNumber":1,//地域价序号
                  "supplyPrice":"",
              }],
            })
        }
      })
      nextProps.itemPerfectVo.preSkuPriceVoList = arrList
      nextProps.itemPerfectVo.preSkuPriceVoList.map(item=>{
        item.supplyStatus = 1
      });
      this.setState({
        column:this.columnsSku,
        initdata : nextProps.itemPerfectVo.preSkuPriceVoList
      })
   // this.props.updateItemTmplAction(nextProps)
    console.log(nextProps)
    } else {
      this.setState({
        column:this.columns,
        initdata : this.initdata
      })
    };
  }

  // 备货及供货价方法
  changeECount=(index,value)=>{
    let itemTmplPublishVo = JSON.parse(JSON.stringify(this.props.itemTmplPublishVo));
    console.log('changed', value);
    console.log('index', index);
    let num;
    if (value || value == 0) {
      num = value;
    }  
    num = parseInt(num);
    if (num != 0 && !num) {
      num = null;
    }
    if(itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index]){
      itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].inventory = num;
    } else {
      itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index] = {};
      itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].inventory = num;
      itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].supplyStatus = 1;
    }    
    this.state.inventory[index]=num;
    this.setState({
      inventory:this.state.inventory
    });
    this.props.updateItemTmplAction(itemTmplPublishVo);
  }

  changeESprice=(value,index)=>{
    let itemTmplPublishVo = JSON.parse(JSON.stringify(this.props.itemTmplPublishVo));
    let num;
      num = value;
      if(itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index]){
        //itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].areaPriceList=[{}],
        itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].areaPriceList[0].supplyPrice = num;
      } else {
        // itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].areaPriceList[0].supplyPrice = num;
        // itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].supplyStatus = 1;
      }    
    //  this.state.supplyPrice[index]=num;
      // this.setState({
      //   supplyPrice: itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].supplyPrice
      // });
      this.props.updateItemTmplAction(itemTmplPublishVo);
  }

  changeBESprice=(e,index)=>{
    let itemTmplPublishVo = JSON.parse(JSON.stringify(this.props.itemTmplPublishVo));
    let num = 0;
    num = e.target.value;
    if(num){
      num = new Number(num);
      num = +num.toFixed(2);
    }
    itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].areaPriceList[0].supplyPrice = num;
   // this.state.supplyPrice[index]=num;
    // this.setState({
    //   supplyPrice: itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].supplyPrice
    // });
    this.props.updateItemTmplAction(itemTmplPublishVo);
  }

  radioChange=(index,e)=>{
    console.log('ind',index,e)
    let itemTmplPublishVo = JSON.parse(JSON.stringify(this.props.itemTmplPublishVo));
    if(itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index]){
      itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].supplyStatus = e.target.value;
    } else {
      itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index] = {};
      itemTmplPublishVo.itemPerfectVo.preSkuPriceVoList[index].supplyStatus = e.target.value;
    } 
    this.props.updateItemTmplAction(itemTmplPublishVo);
  }
  
  render() {
    console.log(this.props,"---")
    const { getFieldDecorator, getFieldValue } = this.props.form;

    return (
      <div>
        <div>
          <div className="rHeaderTtle rMarginBottom"><h2 >供货信息</h2></div>
          <div className="ui-ct">
            <Table 
              columns={this.state.column}
              dataSource={this.state.initdata}
              pagination = {false}
              className="rTable"
              style = {{background: 'white'}}/>
          </div>
        </div>
      </div>
    );
  }
}
