/**
 * 商品发布调用redux 
 * 
 * 发布和编辑发布用两个接口
 * 都要根据不同的页面进行判断
 * */

const ITEM_SUBMIT_LOAD = 'ITEM_SUBMIT_LOAD';
const ITEM_SUBMIT_LOAD_SUCCESS = 'ITEM_SUBMIT_LOAD_SUCCESS';
const ITEM_SUBMIT_LOAD_FAIL = 'ITEM_SUBMIT_LOAD_FAIL';

export default function (state = { loaded: false }, action = {}) {
    switch (action.type) {
        case ITEM_SUBMIT_LOAD:
            return {
                ...state,
                loading: true
            };
        case ITEM_SUBMIT_LOAD_SUCCESS:
            return {
                ...state,
                data: action.result,
                loading: false,
                loaded: true
            };
        case ITEM_SUBMIT_LOAD_FAIL:
            return {
                ...state,
                loading: false,
                loaded: false,
                error: action.error
            };
        default:
            return state;
    }
}

export function querySubmitItem(params, type, edit) {
    let url = null;
    switch (type) {
        case '1': //运营平台
            edit?url = '/item/platform/itemTmplPublish/publishItemByEdit':url = '/item/platform/itemTmplPublish/publishItem';
        case '2': //供应商
            edit?url = '/item/seller/itemTmplPublish/publishItemByEdit':url = '/item/seller/itemTmplPublish/publishItem';
        case '3': //店铺
            edit?url = '/item/shop/itemTmplPublish/publishItemByEdit':url='/item/shop/itemTmplPublish/publishItem';
    }
    return {
        types: [ITEM_SUBMIT_LOAD, ITEM_SUBMIT_LOAD_SUCCESS, ITEM_SUBMIT_LOAD_FAIL],
        promise: (client) => client.get(url, { params: params })
    };
}

